.single .journal .entry-header{
    position: relative;
        h1{
            position: absolute;
            top: 0;
            width: 550px;
            height: 100px;
            font-size: 35px;
            background-color: #248A83;
            text-transform: uppercase;
            padding: 12px 20px 0px 20px;
            line-height: 40px;
        }

        .entry-meta{
            position: absolute;
            bottom: 7px;
            right: 0px;
            width: 300px;
            height: 30px;
            color: white;
            background-color: #248A83;
            padding: 5px;
                a{
                color: white;
            }
        }
}